<template>
    <div id="student-task">
        <!--课程信息-->
        <course-info :course="course"/>

        <div class="main">
            <div class="head">
                <div class="head_info">
                    <span v-if="isCourseWare">课件</span>
                    <span v-if="!isCourseWare">练习</span>【{{taskName}}】</div>
                <div class="head_stats">
                    班级人数：{{total}}，已完成：{{yesComplete}}，未完成：{{noComplete}}
                </div>
                <div class="head_sort">
                    <el-dropdown class="dropdown" @command="handleCommand">
                        <span class="el-dropdown-link">{{taskSortName}}<i class="el-icon-arrow-down el-icon--right"></i></span>
                        <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item v-for="(item,index) in taskSort" :key="index" :command="item">{{item.label}}</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>

            <div class="courseWareTask_list">
                <el-row :gutter="20">
                    <el-col :span="6" v-for="(item, index) in taskDatas" :key="index">
                        <el-card shadow="hover" class="list_card">
                            <el-row :gutter="20">
                                <el-col :span="10">
                                    <img src="/static/boy_img.png" width="70" height="70" alt=""/>
                                </el-col>
                                <el-col :span="14" class="card_info">
                                    <div>{{item.StudentName}}</div>
                                    <div class="card_line">
                                        <div style="margin-top:5px;">{{item.XH}}</div>
                                        <div style="margin-top:8px;">
                                            <span class="completed1" v-if="item.isComplete">已完成</span>
                                            <span class="completed2" v-if="!item.isComplete">未完成</span>
                                        </div>
                                        <div style="margin-top:10px;" v-if="!isCourseWare">
                                            <el-button type="success" size="mini" v-if="item.isComplete" @click="getPracticeDetail(item)">查看答题</el-button>
                                            <el-button type="info" size="mini" v-if="!item.isComplete" plain disabled>查看答题</el-button>
                                        </div>
                                    </div>
                                </el-col>
                            </el-row>
                        </el-card>
                    </el-col>
                </el-row>
            </div>
        </div>

        <el-dialog class="cp_detail" title="答题明细" :visible.sync="dialogDetailVisible">
            <div class="mb" v-for="(item, index) in cpDetail" :key="index">
                <div class="mb">{{item.PSData.PSSerial}}. [{{fetchPSType(item.PSData.PSType)}}] {{item.PSData.PSScore}}分</div>
                <div class="mb">{{item.PSData.PSContent}}</div>
                <div class="mb">
                    <div v-for="(soItem, index) in item.SOList" :key="index" :item="soItem">{{soItem.SOSerial}}. {{soItem.SOContent}}</div>
                </div>
                <div class="mb">
                    答案：
                    <span class="primary">{{item.PSData.PSAnswer}}</span>
                </div>
                <div class="mb">
                    解析：
                    <span class="danger">{{item.PSData.PSanalysis}}</span>
                </div>

                <div class="part_title">学生答题</div>
                <div v-html="item.PAContent"></div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
 import courseInfo from "../../../../../components/personal/teacher/myCourse/courseInfo.vue";
 import api from "../../../../../utils/api"; 
export default {
    components: {
      courseInfo 
    },
    data() {
        return {
            course: JSON.parse(sessionStorage.getItem("course")),
            courseWare: JSON.parse(sessionStorage.getItem("courseWare")),
            ClassPractice: JSON.parse(sessionStorage.getItem("classPractice")),
            taskSort: [
                { label: "全部", value: 0 },
                { label: "已完成", value: 1 },
                { label: "未完成", value: 2 },
            ],
            taskDatas: [],
            taskName: "",
            taskSortName: "全部",
            isCourseWare: "",
            total: 0,
            yesComplete: 0,
            noComplete: 0,
            dialogDetailVisible: false,
            cpDetail: []
        }
    },
    created() {
      this.initData();
    },
    methods: {
        initData() {
            this.isCourseWare = sessionStorage.getItem("taskType") == "CourseWare" ? true : false;
            if(this.isCourseWare) {
                this.taskName = this.courseWare.name;
                this.getCTDatas(0);
            }else if(!this.isCourseWare) {
                this.taskName = this.ClassPractice.title;
                this.getPSDatas(0);
            }
        },

        handleCommand(command) {
            this.taskSortName = command.label;
            if(this.isCourseWare) {
                this.getCTDatas(command.value);
            }else if(!this.isCourseWare) {
                this.taskName = this.ClassPractice.title;
                this.getPSDatas(command.value);
            }
        },

        //获取课件任务
        getCTDatas(sort) {
            this.taskDatas = [];
            this.$http
                .get(this.$api.GetCoursewareTask, { classID: this.course.ClassID, cID: this.courseWare.id, sort: sort })
                .then(res => {
                    if (res.Result && res.Datas && res.Datas.length > 0) {
                        res.Datas.forEach(item => {
                            this.taskDatas.push({
                                StudentName: item.Key["StudentName"],
                                XH: item.XH,
                                isComplete: item.CTID == "" ? false : true
                            });
                        });

                        this.total = res.Total;
                        this.yesComplete = res.YesComplete;
                        this.noComplete = res.NoComplete;
                    }
                })
                .catch(err => {
                    
                });
        },

        //获取练习任务
        getPSDatas(sort) {
            this.taskDatas = [];
            this.$http
                .get(this.$api.ClassPractiseGetTask, { classID: this.course.ClassID, cpID: this.ClassPractice.id, sort: sort })
                .then(res => {
                    if (res.Result && res.Datas && res.Datas.length > 0) {
                        res.Datas.forEach(item => {
                            this.taskDatas.push({
                                StudentID: item.Key["StudentID"],
                                StudentName: item.Key["StudentName"],
                                XH: item.XH,
                                PSID: item.PSID,
                                isComplete: item.PSID == "" ? false : true
                            });
                        });

                        this.total = res.Total;
                        this.yesComplete = res.YesComplete;
                        this.noComplete = res.NoComplete;
                    }
                })
                .catch(err => {
                    
                });
        },

        //获取学生答题
       async getPracticeDetail(row) {
            this.cpDetail = [];
            this.dialogDetailVisible = true;
            let downloadURL = await api.GetI_DownloadFileURL();
            this.$http
                .get(this.$api.GetCPStudentAnswer, { cpID: this.ClassPractice.id, studentID: row.StudentID })
                .then(res => {
                    if (res.Result && res.Datas && res.Datas.length > 0) {
                        res.Datas.forEach(item => { 
                            var newContent = "未作答";
                            if(item.PAData != null) {
                                var htmstr = item.PAData.PAContent;
                                newContent = htmstr.replace(
                                /<img [^>]*src=['"]([^'"]+)[^>]*>/gi,
                                function(match, capture) {  
                                    var filename = capture.split('/')[1]; 
                                    var newStr = '<img src="' + downloadURL + '?filename=' + filename + '&fileType=7" alt="" style="width:380px"/>';
                                    return newStr;
                                }
                            ); 
                            }  

                            this.cpDetail.push({
                                PSData: item.PSData,
                                PAContent: newContent,
                                SOList: item.SOList
                            }); 
                        });
                    }
                })
                .catch(err => {
                    
                });
        },

        //获取题目类型
        fetchPSType(val) {
            let typeName = "";
            switch (val) {
            case 1:
                typeName = "单选题";
                break;
            case 2:
                typeName = "多选题";
                break;
            case 3:
                typeName = "判断题";
                break;
            case 4:
                typeName = "问答题";
                break;
            }

            return typeName;
        },
    }
}
</script>

<style lang='less'>
#student-task {
    text-align: left;
    .top {
        width: 955px;
        background: rgba(255, 255, 255, 1);
        border-radius: 3px;
        padding: 20px;
        font-size: 14px;
        color: #333;
        margin-bottom: 20px;
        overflow: hidden;

        .tt {
            margin-bottom: 27px;
        }
        .lname {
            margin-bottom: 25px;
        }
        .info {
            font-size: 12px;
            display: flex;
            justify-content: space-between;

            i {
            margin-right: 10px;
            color: #2a9efc;
            }
        }
    }

    .main {
        width: 956px;
        background: rgba(255, 255, 255, 1);
        border-radius: 0px;
        padding: 20px;
        .head {
            margin-bottom: 40px;
        
            .head_info {
                float: left;
            }
            .head_stats {
                float: left;
                margin-left: 50px;
            }
            .head_sort {
                float: right;
            }
        }

        .courseWareTask_list {
            clear: both;
            .list_card {
                text-align: left;
                margin-bottom: 15px;
                .card_info {
                    color: #2c3e50;
                    .card_line {
                        font-size: 14px;
                    }
                }
                .completed1{
                    color: #67C23A;
                }
                .completed2{
                    color: #E6A23C;
                }
            }
        }
    }

    .cp_detail {
        .cp_student {
            margin-bottom: 20px;
            font-size: 15px;
        }

        .mb {
            margin-bottom: 15px;
            .primary {
                color: #409eff;
            }

            .danger {
                color: #f56c6c;
            }
        }

        .part_title {
            border-left: 2px solid #2a9efc;
            padding-left: 5px;
            margin: 5px 0;
        }

        .el-dialog__body {
            padding: 0px 20px 10px 20px;
        }
    }
}
</style>



